import React from 'react';
import Header from './Header';
import Order from './Order';
import Inventory from './Inventory';
import sampleFishes from '../sample-fishes';
import Fish from './Fish';
import base from '../base';

class App extends React.Component {
  // state 的宣告，必須使用 state 關鍵字
  state = {
    fishes: {},
    order: {}
  };

  // 將 state 映射至 Firebase 的 Database
  componentDidMount() {
    // 先抓一下當下的店名
    // 批次取值的方法，來自 ES6 structuring 的巧技
    const { params } = this.props.match;

    // 這個 ref 不是 react 的 input 用的 ref，而是 Firebase 的 Database 的資料切片
    this.fishesRef = base.syncState(`${params.storeId}/fishes`, {
      context: this,
      state: 'fishes'
    });
    // this.orderRef = base.syncState(`${params.storeId}/order`, {
    //   context: this,
    //   state: 'order'
    // });

    // 讀取 localStorage 內的 order
    const localStorageOrder = localStorage.getItem(params.storeId);
    if (localStorageOrder) {
      this.setState({ order: JSON.parse(localStorageOrder) });
    }
  }

  componentDidUpdate() {
    // 使用 localStorage 來存放 order
    const { params } = this.props.match;
    localStorage.setItem(params.storeId, JSON.stringify(this.state.order));
  }

  componentWillUnmount() {
    // 退出頁面時，要記得停止 Firebase 的同步，減少記憶體的占用
    base.removeBinding(this.fishesRef);
  }

  // 新增資料到 state
  addFish = fish => {
    const fishes = { ...this.state.fishes }; // 複製物件的巧技
    fishes[`fish${Date.now()}`] = fish;
    this.setState({ fishes }); // setState 為異動 state 的方法。當目標與來源為同類物件時，寫目標即可，係 ？？？ 之巧技。TODO: 待查巧技所依據。
  };

  editFish = (key, updatedFish) => {
    const fishes = { ...this.state.fishes };
    fishes[key] = updatedFish;
    this.setState({ fishes });
  };

  addToOrder = key => {
    const order = { ...this.state.order };
    order[key] = order[key] + 1 || 1; // 切換『累加|新增』的巧技
    this.setState({ order });
  };

  deleteFromOrder = key => {
    const order = { ...this.state.order };
    // 一般刪除法
    delete order[key];
    this.setState({ order });
  };

  deleteFish = key => {
    const fishes = { ...this.state.fishes };
    // 配合 Firebase 的刪除機制，採用 null 值來刪除
    fishes[key] = null;
    this.setState({ fishes });
  };

  loadSampleFishes = () => {
    this.setState({ fishes: sampleFishes });
  };

  render() {
    return (
      <div className="catch-of-the-day">
        <div className="menu">
          {/* prob 可自訂名稱，其值若非字串，請使用 {} 來包裹 */}
          <Header tagline="fresh seafood market" />
          <ul className="fishes">
            {/* for loop in jsx */}
            {/* 應 React 的要求，物件內必須有 key 屬性 */}
            {/* 子元件內部得不到 key 值，故另傳 index 屬性來傳 key 值 */}
            {Object.keys(this.state.fishes).map(key => (
              <Fish
                key={key}
                index={key}
                data={this.state.fishes[key]}
                addToOrder={this.addToOrder}
              />
            ))}
          </ul>
        </div>
        <Order
          fishes={this.state.fishes}
          order={this.state.order}
          deleteFromOrder={this.deleteFromOrder}
        />
        <Inventory
          storeId={this.props.match.params.storeId}
          fishes={this.state.fishes}
          addFish={this.addFish}
          updateFish={this.editFish}
          deleteFish={this.deleteFish}
          loadSampleFishes={this.loadSampleFishes}
        />
      </div>
    );
  }
}

export default App;
