import React from 'react';
import { formatPrice } from '../helpers';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import PropTypes from 'prop-types';

class Order extends React.Component {
  static propTypes = {
    fish: PropTypes.shape({
      image: PropTypes.string.isRequired,
      name: PropTypes.string.isRequired,
      desc: PropTypes.string.isRequired,
      status: PropTypes.string.isRequired,
      price: PropTypes.number.isRequired
    })
  };

  renderOrder = key => {
    const fish = this.props.fishes[key];
    const count = this.props.order[key];
    const orderAnimate = {
      classNames: 'order',
      key,
      timeout: { enter: 500, exit: 500 }
    };
    const countAnimate = {
      classNames: 'count',
      timeout: { enter: 500, exit: 500 }
    };

    // 在 fish 還未取得時，先不顯示內容
    if (!fish) {
      return null;
    }

    const isAvailable = fish.status === 'available';
    if (!isAvailable) {
      return (
        <CSSTransition {...orderAnimate}>
          <li key={key}>
            {fish.name} is out of stock
            <button onClick={() => this.props.deleteFromOrder(key)}>
              &times;
            </button>
          </li>
        </CSSTransition>
      );
    }
    return (
      <CSSTransition {...orderAnimate}>
        <li key={key}>
          <span>
            <TransitionGroup component="span" className="count">
              {/* 將前後會有差異的內容放置於 key 內，使 transition 能區別前後元件 */}
              <CSSTransition key={count} {...countAnimate}>
                <span>{count}</span>
              </CSSTransition>
            </TransitionGroup>
            lbs {fish.name}
            <button onClick={() => this.props.deleteFromOrder(key)}>
              &times;
            </button>
          </span>
          <TransitionGroup component="span" className="count">
            <CSSTransition key={count} {...countAnimate}>
              <div>{formatPrice(count * fish.price)}</div>
            </CSSTransition>
          </TransitionGroup>
        </li>
      </CSSTransition>
    );
  };

  render() {
    const orderIds = Object.keys(this.props.order);
    const total = orderIds.reduce((pre, key) => {
      const fish = this.props.fishes[key];
      const count = this.props.order[key];
      const isAvailable = fish && fish.status === 'available';
      if (isAvailable) {
        return pre + count * fish.price;
      }
      return pre;
    }, 0);
    return (
      <div className="order-wrap">
        <h2>Order</h2>
        {/* CSS 動畫最外框 */}
        <TransitionGroup component="ul" className="order">
          {/* 可將內容放置於功能變數中，以簡化語法部局 */}
          {orderIds.map(this.renderOrder)}
        </TransitionGroup>
        <div className="total">
          <strong>Total: {formatPrice(total)}</strong>
        </div>
      </div>
    );
  }
}

export default Order;
